.MySwitch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.MySwitch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.MySwichSlider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.MySwichSlider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    right: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .MySwichSlider {
    background-color: #f87171;
}

input:focus + .MySwichSlider {
    box-shadow: 0 0 1px #f87171;
}

input:checked + .MySwichSlider:before {
    -webkit-transform: translateX(-26px);
    -ms-transform: translateX(-26px);
    transform: translateX(-26px);
}

/* Rounded MySwichSliders */
.MySwichSlider.round {
    border-radius: 34px;
}

.MySwichSlider.round:before {
    border-radius: 50%;
}

.swiper-button-next {
    left: 0px !important;
    padding: 30px;
    height: 110% !important;
    top: 0 !important;
    bottom: 0 !important;
    color: #fff !important;
    fill: #fff !important;
    stroke: black !important;
}
.swiper-button-prev {
    right: 0px !important;
    padding: 30px;
    height: 110% !important;
    top: 0 !important;
    bottom: 0 !important;
    color: #fff !important;
    fill: #fff !important;
    stroke: black !important;
}

.verticallySlider .swiper .swiper-pagination .swiper-pagination-bullet {
    @apply bg-red-400 scale-150 my-3;
}
.verticallySlider .swiper-slide-active {
    @apply opacity-100  !important;
}

.verticallySlider .swiper-slide {
    @apply md:opacity-30 shadow-none opacity-100;
}

.verticallySlider .swiper-pagination {
    right: 5px !important;
}

.fav-city-box ::-webkit-scrollbar {
    width: 5px;
}

/* Track */
.fav-city-box ::-webkit-scrollbar-track {
